import React from "react";

const Header = React.lazy(() => import("@Components/Header/Header"));
const PromotionProduct = React.lazy(() => import("@Components/PromotionProduct/PromotionProduct"));
const ProductList = React.lazy(() => import("@Components/ProductList/ProductList"));
const ProductCategory = React.lazy(() => import("@Components/ProductCategory/ProductCategory"));
const Footer = React.lazy(() => import("@Components/Footer/Footer"));

const App = () => {
  return (
    <>
      <div style={{ position: "fixed", top: 0, width: "100%", zIndex: 16 }}>
        <Header />
      </div>
      <div style={{ paddingTop: "125px" }}>
        <PromotionProduct />
        <ProductCategory />
        <ProductList />
        <Footer /> 
      </div>
    </>
  );
};

export default App;
